@import 'variables';
@import '_Search';
@import '_Artist';
@import '_Trackslider';
@import '_Navbar';
@import '_About';
@import '_Footer';

html {
  scroll-behavior: smooth;
  height: 100%;
  margin: 0

}
body {
  background-color: rgb(179, 222, 253);
  color: rgb(38, 36, 36);
  margin: 0;
  height: 100vh;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
}

* {
  margin: 0
}

h1 {
  font-size: 50px;
  color: rgb(19, 11, 81);
  font-family: "Expletus Sans", serif;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.page-not-found {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;

  p {
      position: absolute;
      color: rgb(0, 0, 0);
      font-family: "Inconsolata", serif;
      padding: 2px 5px;
      opacity: 0.9;
      background-color: rgb(247, 190, 199);
      border-radius: 5px;
    }

  img {
    height: 250px;
    border: 5px solid pink;
    border-radius: 15px;

    @media screen and (max-width: 449px) {
      height: 170px
    }
  }

}